* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Arial", sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #fff;
}

.App {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 0px;
}

html {
  scroll-behavior: smooth;
}

/* Адаптивность */
@media (max-width: 768px) {
  .App {
    padding: 0 15px;
  }
}
